.card {
  box-shadow: 7px 7px 5px 1.25px rgba(0, 0, 0, 0.25);
  border-radius: 0.25rem;
  max-width: 320px !important;
  margin-bottom:30px;
}

.card-title {
  text-align: left;
  color: var(--blue_);
}
.card-info-container{
    justify-content: space-around;
}
.card-body{
    padding-top:0 !important;
}
.card-text {
  text-align: left;
  color: var(--blue_);
  font-weight: 600;
  margin-bottom: 0.5rem !important;
  font-size: .9em;  
}
.sizes{
  margin-bottom: 0.15rem !important;
}

.height {
  margin-left: 65px;
}

.price {
  color: var(--red_);
  font-weight: bold;
  font-size: 2em;
}
